var Template = [
  `
<!DOCTYPE html>
<html lang="en">
  <head>
    <meta charset="UTF-8" />
    <meta name="viewport" content="width=device-width, initial-scale=1.0" />
    <title>InteractiveDoc</title>
  </head>
  <style>
    .result-container {
      font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI",
        Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji",
        "Segoe UI Emoji", "Segoe UI Symbol", "Microsoft YaHei Light", sans-serif;
      display: flex;
      flex-direction: column;
      width: 70%;
      margin: 0 auto;
      text-align: left;
      overflow: scroll;
    }

    .katex-html {
      display: none;
    }

    /* Стили для заголовков h1-h6 */
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      margin: 10px auto;
      font-size: large;
      font-weight: 700;
      border: none;
      color: black;
      opacity: 0.9;
    }

    body {
        background-color: #d9d9d9;
    }

    h1 {
      font-size: 24pt;
      line-height: 1.2;
    }

    h2 {
      font-size: 22pt;
      line-height: 1.3;
    }

    h3 {
      font-size: 20pt;
      line-height: 1.4;
    }

    h4 {
      font-size: 18pt;
      line-height: 1.5;
    }

    h5 {
      font-size: 16pt;
      line-height: 1.6;
    }

    h6 {
      font-size: 14pt;
      line-height: 1.7;
    }

    /* Стили для абзацев */
    p,
    li {
      font-size: 14pt;
      line-height: 1.6;
      color: black;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    ul {
      background-color: #c0c0c0;
      border-radius: 10px;
    }

    li::marker {
      color: #fff;
    }

    /* Стили для таблиц */
    table {
      font-size: 14pt;
      border-collapse: collapse;
      width: 100%;
      margin-top: 20px;
      margin-bottom: 40px;
    }

    table td,
    table th {
      border: 1px solid #ddd;
      padding: 8px;
    }

    table th {
      background-color: #f5f5f5;
      font-weight: bold;
    }

    /* Дополнительные стили для таблицы, если нужно */
    table th.text-left,
    table td.text-left {
      text-align: left;
    }

    table th.text-center,
    table td.text-center {
      text-align: center;
    }

    table th.text-right,
    table td.text-right {
      text-align: right;
    }

    ::-webkit-scrollbar {
      width: 10px;
      height: 10px;
    }
    ::-webkit-scrollbar-button:start:decrement,
    ::-webkit-scrollbar-button:end:increment {
      height: 10px;
      background-color: transparent;
    }
    ::-webkit-scrollbar-track-piece {
      background-color: transparent;
      -webkit-border-radius: 16px;
    }
    ::-webkit-scrollbar-thumb:vertical {
      height: 10px;
      background-color: #666;
      -webkit-border-radius: 16px;
    }

    ::-webkit-scrollbar-thumb:vertical {
      display: none; /* Скрываем вертикальный ползунок */
    }

    ::-webkit-scrollbar-thumb:horizontal {
      display: none; /* Скрываем горизонтальный ползунок */
    }

    ::-webkit-scrollbar-corner {
      display: none;
    }

    input[type="checkbox"] {
  position: relative;
  cursor: pointer;
}
    input[type="checkbox"]:before {
      content: "";
      display: block;
      position: absolute;
      width: 16px;
      height: 16px;
      top: 0;
      left: 0;
      border: 2px solid #555555;
      border-radius: 3px;
      background-color: white;
    }
    input[type="checkbox"]:checked:after {
      content: "";
      display: block;
      width: 5px;
      height: 10px;
      border: solid black;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      top: 2px;
      left: 6px;
    }


    .quiz {
      background-color: #fff;
      margin: 20px 0;
      padding: 20px;
      border-radius: 10px;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    }

    .question {
      font-size: 24px;
      margin-bottom: 20px;
      transition: opacity 0.3s ease;
    }

    .options {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }

    .option {
      cursor: pointer;
      padding: 10px;
      border-radius: 5px;
      transition: background-color 0.3s ease;
    }

    .option:hover {
      background-color: #f2f2f2;
    }

    .option.show-correct {
      background-color: #8bc34a;
      color: #fff;
    }

    .option.show-incorrect {
      background-color: #c84131;
      color: #fff;
    }

    .hidden {
      display: none;
    }

    .hide {
      position: relative;
      animation-name: test_css_moving_sideways;
      animation-duration: 3s;
      animation-iteration-count: 1;
      animation-direction: linear;
      left: 0%;
      animation-delay: 2s;
      animation-fill-mode: none, forwards;
    }

    code {
      font-size: 14pt;
      border-radius: 10px;
      padding: 20px !important;
    }

    iframe {
      background-color: black;
      width: 100%;
    }

    img {
      width: 100%;
    }
    .hljs {
      display: block;
      overflow-x: auto;
      padding: 0.5em;
      background: #282828;
    }
    .hljs,
    .hljs-subst {
      color: #ebdbb2;
    }
    .hljs-deletion,
    .hljs-formula,
    .hljs-keyword,
    .hljs-link,
    .hljs-selector-tag {
      color: #fb4934;
    }
    .hljs-built_in,
    .hljs-emphasis,
    .hljs-name,
    .hljs-quote,
    .hljs-strong,
    .hljs-title,
    .hljs-variable {
      color: #83a598;
    }
    .hljs-attr,
    .hljs-params,
    .hljs-template-tag,
    .hljs-type {
      color: #fabd2f;
    }
    .hljs-builtin-name,
    .hljs-doctag,
    .hljs-literal,
    .hljs-number {
      color: #8f3f71;
    }
    .hljs-code,
    .hljs-meta,
    .hljs-regexp,
    .hljs-selector-id,
    .hljs-template-variable {
      color: #fe8019;
    }
    .hljs-addition,
    .hljs-meta-string,
    .hljs-section,
    .hljs-selector-attr,
    .hljs-selector-class,
    .hljs-string,
    .hljs-symbol {
      color: #b8bb26;
    }
    .hljs-attribute,
    .hljs-bullet,
    .hljs-class,
    .hljs-function,
    .hljs-function .hljs-keyword,
    .hljs-meta-keyword,
    .hljs-selector-pseudo,
    .hljs-tag {
      color: #8ec07c;
    }
    .hljs-comment {
      color: #928374;
    }
    .hljs-link_label,
    .hljs-literal,
    .hljs-number {
      color: #d3869b;
    }
    .hljs-comment,
    .hljs-emphasis {
      font-style: italic;
    }
    .hljs-section,
    .hljs-strong,
    .hljs-tag {
      font-weight: 700;
    }
  </style>
  <body><div class="result-container">`,
  `</div></body>
  <script>
    var options = document.querySelectorAll(".option");

    for (var i = 0; i < options.length; i++) {
      options[i].addEventListener("click", function () {
        var selectedOption = this;
        if (selectedOption.classList.contains("correct")) {
          selectedOption.classList.add("show-correct");
        } else {
          selectedOption.classList.add("show-incorrect");
        }
      });
    }

    var hide = document.querySelectorAll(".hide");
    for (var i = 0; i < hide.length; i++) {
      var prevBack = hide[i].style.backgroundColor;
      var prevColor = hide[i].style.color;
      hide[i].style.display = "inline-block";
      hide[i].style.minWidth = "30px";
      hide[i].style.backgroundColor = "#808080";
      hide[i].style.borderRadius = "10px";
      hide[i].style.color = "#808080";
      hide[i].style.cursor = "pointer";
      hide[i].addEventListener("click", function () {
        this.style.display = "inline";
        this.style.backgroundColor = "transparent";
        this.style.color = prevColor;
      });
    }

    var checkboxes = document.querySelectorAll('input[type="checkbox"]');

    for (let index = 0; index < checkboxes.length; index++) {
      checkboxes[index].disabled = false;
      checkboxes[index].addEventListener("click", function () {
        this.hasAttribute("checked")
          ? this.removeAttribute("checked")
          : this.setAttribute("checked", "checked");
      });
    }
  </script>
</html>

`,
];

export default Template;
