import { createStore } from "vuex";
import Template from "./downloaded";
import helpInteractive from "../store/help";

const showdown = require("showdown");
const showdownKatex = require("showdown-katex");
const showdownHighlight = require("showdown-highlight");
require("showdown-youtube");

const converter = new showdown.Converter({
  simplifiedAutoLink: true,
  tasklists: true,
  tables: true,
  strikethrough: true,
  ghCodeBlocks: true,
  extensions: [
    showdownKatex({
      displayMode: true,
      throwOnError: false, // allows katex to fail silently
      errorColor: "#ff0000",
      delimiters: [
        { left: "$", right: "$", display: true },
        { left: "~", right: "~", display: true, asciimath: true },
      ],
    }),
    showdownHighlight({
      auto_detection: true,
    }),
    "youtube",
  ],
});

export default createStore({
  state: {
    text: "",
    htmlFromText: "",
    doc: "<center><h1>Здесь будет результат!</h1></center>",
    colorTheme: {
      dark: {
        mainColor: "#000",
        secondColor: "#fff",
        background: "#d9d9d9",
        navAndAreaColor: "#b1b8ca",
        buttonsColor: "#4f5e9c",
      },
      light: {
        mainColor: "#fff",
        secondColor: "#000",
        background: "#d9d9d9",
        navAndAreaColor: "#b1b8ca",
        buttonsColor: "#4f5e9c",
      },
    },
  },
  getters: {},
  mutations: {
    openDocument(state) {
      const fileInput = document.createElement("input");
      fileInput.setAttribute("type", "file");
      fileInput.setAttribute("id", "fileInput");
      fileInput.setAttribute("accept", ".int");
      fileInput.style.display = "none";

      document.body.appendChild(fileInput);

      fileInput.addEventListener("change", function (event) {
        const file = event.target.files[0];

        const reader = new FileReader();
        reader.onload = function (e) {
          const documentText = e.target.result;
          // Use the document text here, store it in a variable, or perform further operations.
          state.text = documentText;
        };

        reader.readAsText(file);

        // Remove the file input element from the DOM
        document.body.removeChild(fileInput);
      });

      // Trigger the click event on the file input element
      fileInput.click();
    },
    downloadDocument(state) {
      var result = document.querySelector(".result-container").innerHTML;
      function downloadStringAsFile(text) {
        const element = document.createElement("a");
        element.setAttribute(
          "href",
          "data:text/plain;charset=utf-8," + encodeURIComponent(text)
        );
        element.setAttribute("download", "document.int");
        element.style.display = "none";
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
      }
      downloadStringAsFile(state.text);
    },
    downloadProject() {
      var result = document.querySelector(".result-container").innerHTML;
      function downloadStringAsFile(text) {
        const element = document.createElement("a");
        element.setAttribute(
          "href",
          "data:text/plain;charset=utf-8," + encodeURIComponent(text)
        );
        element.setAttribute("download", "document.html");
        element.style.display = "none";
        document.body.appendChild(element);
        element.click();
        document.body.removeChild(element);
      }

      const interactiveProject = Template[0] + result + Template[1];
      downloadStringAsFile(interactiveProject);
    },
    printDocument() {
      const navBar = document.querySelector("nav");
      navBar.style.display = "none";
      const textArea = document.querySelector("textarea");
      textArea.style.display = "none";
      const resultContainer = document.querySelector(".result-container");
      resultContainer.style.width = "100%";
      window.print();
      resultContainer.style.width = "45%";
      navBar.style.display = "flex";
      textArea.style.display = "block";
    },
    helpInteractive(state) {
      state.text = helpInteractive;
    },
    compileDocument(state) {
      const regex = /\\quiz(.+?\n)+/gim;

      var quizArrayIndex;
      var answerIndex;
      var index;
      var findingsInText = state.text.match(regex);
      var findings;
      if (findingsInText) {
        var findings = [...findingsInText];
      }

      var k = -1;
      state.htmlFromText = state.text;
      for (quizArrayIndex in findings) {
        k += 1;
        findings[quizArrayIndex] = findings[quizArrayIndex]
          .split("\n")
          .map((item) => {
            return item.replace("\\quiz", "");
          })
          .filter((item) => {
            return item;
          });

        let quiz = `<div class="quiz">
              <div class="question">
                ${findings[quizArrayIndex][0]}
              </div>`;
        for (answerIndex in findings[quizArrayIndex].slice(1)) {
          var answer = findings[quizArrayIndex].slice(1)[answerIndex];
          if (answer.startsWith(":")) {
            quiz += `<div class="option correct">${answer.slice(1)}</div>`;
          } else {
            quiz += `<div class="option">${answer}</div>`;
          }
        }
        quiz += `  </div>\n`;

        state.htmlFromText = state.htmlFromText.replace(
          findingsInText[quizArrayIndex],
          quiz
        );
      }
      // Add new function in this box
      state.htmlFromText = state.htmlFromText.replace(
        /\\h{(.+?)}/gim,
        "<span class='hide'>$1</span>"
      );
      state.htmlFromText = converter.makeHtml(state.htmlFromText);

      state.doc = state.htmlFromText;
    },
  },
  actions: {},
  modules: {},
});
